import { useForm } from "react-hook-form";
import { useRecoilValue, useSetRecoilState } from "recoil";
import { Categories, categoryState, toDoState } from "./atoms";

interface IForm {
	createCategory: string;
}

function CreateCategory() {
	const setCategories = useSetRecoilState(Categories);
	const category = useRecoilValue(categoryState);
	const { register, handleSubmit, setValue } = useForm<IForm>();
	const handleValid = ({ createCategory }: IForm) => {
		setCategories((oldCategories) => [...oldCategories, createCategory]);
		setValue("createCategory", "");
	};
	return (
		<form onSubmit={handleSubmit(handleValid)}>
			<input
				{...register("createCategory", {
					required: "Please enter New Category name",
				})}
				placeholder="Enter new Category name"
			/>
			<button>CREATE</button>
		</form>
	);
}

export default CreateCategory;
