import { useRecoilState, useRecoilValue } from "recoil";
import { toDoSelector } from "./atoms";
import CreateToDo from "./CreateToDo";
import ToDo from "./ToDo";

import tw from "twin.macro";
import styled from "styled-components";

const Wrapper = styled.main.attrs({
	className: "flex justify-center items-center bg-gray-100",
})`
	& {
		form {
			${tw`flex items-center justify-center`}
		}
		input {
			${tw`border-gray-300 mt-10 mb-5 w-2/3 items-center border-solid border rounded-full py-2 px-4 mr-3`}
		}
		button {
			${tw`bg-[#6B9B7D] hover:bg-[#567D65] mt-5 text-white font-bold py-1.5 px-6 rounded-full`}
		}
	}
`;

const ToDoBack = styled.div`
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
`;

const ToDoLi = styled.div`
	display: flex;
	width: 340px;
	height: 120px;
	background-color: #f7f7fb;
	border-radius: 5px;
	margin-bottom: 10px;
	padding: 10px;
	margin-left: 15px;
`;

const ToDoText = styled.div`
	width: 100%; ;
`;

function ToDoList() {
	const toDos = useRecoilValue(toDoSelector);

	return (
		<div>
			<Wrapper>
				<CreateToDo />
			</Wrapper>
			<ToDoBack>
				{toDos?.map((toDo) => (
					<ToDoLi>
						<ToDoText>
							<ToDo key={toDo.id} {...toDo} />
						</ToDoText>
					</ToDoLi>
				))}
			</ToDoBack>
		</div>
	);
}
export default ToDoList;
